<template>
  <v-app>
    <template v-if="isLogged()">
      <!-- <core-filter /> -->

      <core-toolbar />

      <core-drawer />

      <core-view />
    </template>
    <template v-else>
      <login />
    </template>
  </v-app>
</template>

<script>
import { LOCALSTORAGE } from './constants'

const login = () => import('./views/Login.vue')

export default {
  metaInfo: {
    title: 'Pr√°tico ',
    titleTemplate: '%s',
    htmlAttrs: {
      lang: 'en',
      amp: true
    }
  },
  components: { login },
  methods: {
    isLogged () {
      const token = localStorage.getItem(LOCALSTORAGE.JWT)
      return !!token
    }
  }
}
</script>

